<template>
  <div>
    <el-card style="width: 40%;margin: 10px">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="用户名">
          <el-input v-model="form.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.nickName"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item label="年龄" v-show="this.isShow">
          <el-input v-model="form.age"></el-input>
        </el-form-item>
        <el-form-item label="性别" v-show="this.isShow">
          <el-radio v-model="form.sex" label="男">男</el-radio>
          <el-radio v-model="form.sex" label="女">女</el-radio>
          <el-radio v-model="form.sex" label="未知">未知</el-radio>
        </el-form-item>
        <el-form-item label="手机号" v-show="this.isShow">
          <el-input v-model="form.tel"></el-input>
        </el-form-item>
        <el-form-item label="地址"  v-show="this.isShow">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
      </el-form>
      <div style="text-align: center">
        <el-button type="primary" @click="update">保存</el-button>
      </div>

    </el-card>

  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "Person",

  data(){
    return{
      form:[],
      isShow:true,
    }
  },
  created() {
    let str = sessionStorage.getItem("user") || "{}"
    this.form = JSON.parse(str)
    if(this.form.role === '0'){
      //管理员登录
      this.isShow = false
    }else{
      this.isShow = true
    }
  },
  methods:{
    update(){
      let url = ''
      if(this.form.role === '0'){
        //管理员登录
        url = "/root"
      }else if(this.form.role === '1'){
        //病人登录
        url = "/patient"
      }else{
        //专家登录
        url = "/specialist"
      }
      request.put(url,this.form).then(res => {
        if(res.code === '0'){
          this.$message({
            type:"success",
            message:"更新成功"
          })
          sessionStorage.setItem("user",JSON.stringify(this.form))
        }else{
          this.$message({
            type:"error",
            message:res.message
          })
        }
      })
    }
  },
}
</script>

<style scoped>

</style>
